import React, { Component, Suspense } from 'react';
import { toast } from "react-toastify";
import Swal from 'sweetalert2';
import { connect } from "react-redux";
import { getCollectionList } from '../../../Services/Actions/homeActions'
import { updatePublishRequest, getPublishList, publishDeleteRequest } from '../../../Services/Actions/publishActions'

const HtmlEmbed = React.lazy(() => import('./Components/EmbedPlatforms/HtmlEmbed'));
const WordPressEmbed = React.lazy(() => import('./Components/EmbedPlatforms/WordPressEmbed'));
const WixEmbed = React.lazy(() => import('./Components/EmbedPlatforms/WixEmbed'));
const SquarespaceEmbed = React.lazy(() => import('./Components/EmbedPlatforms/SquarespaceEmbed'));
const ShopifyEmbed = React.lazy(() => import('./Components/EmbedPlatforms/ShopifyEmbed'));
const OtherEmbed = React.lazy(() => import('./Components/EmbedPlatforms/OtherEmbed'));

const WebEmbedAside = React.lazy(() => import('./Components/WebEmbedAside'));
const NeedSupport = React.lazy(() => import('../CommonComponents/NeedSupport'));

const dataWithComma = (updateData) => {
    var dataCollection = {}
    if (updateData && String(updateData).length > 0)
        String(updateData).split(",").map((item) => {
            dataCollection[item] = true
        })
    return dataCollection
}
const tagDataWithComma = (updateData) => {
    var dataCollection = []
    if (updateData && String(updateData).length > 0)
        String(updateData).split(",").map((item) => {
            dataCollection.push(item)
        })
    return dataCollection
}
const assetsData = (data) => {
    var dataCollection = {}
    if (data && String(data).length > 0) {
        if (String(data).includes("2,4")) {
            dataCollection["2,4"] = true
        }
        else if (String(data).includes("3,5")) {
            dataCollection["3,5"] = true
        }
        if (String(data).includes("1")) {
            dataCollection["1"] = true
        }
    }

    return dataCollection
}

const chooseActiveFilter = (assetsType, collection, connection, source, tags, ugcrights) => {
    var dataCollection = {}
    if (assetsType) {
        dataCollection["assetsType"] = true
    }
    if (collection) {
        dataCollection["collection"] = true
    }
    if (connection) {
        dataCollection["connection"] = true
    }
    if (source) {
        dataCollection["source"] = true
    }
    if (tags) {
        dataCollection["tags"] = true
    }
    if (ugcrights) {
        dataCollection["ugcrights"] = true
    }
    return dataCollection
}

class WebEmbed extends Component {
    constructor(props) {
        super(props);
        const { item, templeteList } = props
        var templateData = {}
        if (templeteList && templeteList.length > 0) {
            const DataTemp = templeteList.filter((temp) => temp.id == item.template)
            if (DataTemp && DataTemp.length > 0) templateData = DataTemp[0]
            else templateData = { id: item.template }

        }
        else templateData = { id: item.template }
        this.state = {
            showGenratedCode: 'p-html',
            chooseFilter: chooseActiveFilter(item.assets_type, item.collection, item.source, item.network, item.tags, item.ugc_status),
            collectionApply: [],
            sourceApply: {},
            assetsApply: assetsData(item.assets_type),
            tagApply: tagDataWithComma(item.tags),
            ugcRigths: {},
            selectedTemplate: templateData,
            condition: String(item.filterType).toLowerCase(),
            networkData: [],
            feedData: [],
            selectedNetwork: dataWithComma(item.network),
            selectedConnection: dataWithComma(item.source),
            textSearch: "",
            textConnectionSearch: "",
            collectionList: [],
            collectionCompleteList: [],
            selectedCollection: dataWithComma(item.collection),
            collectionSearch: "",
            genrateCode: false,
            publishList: [],
            responseData: item,
            activeFilter: null,
            selectedWebEmbed: null,
            publishCodeName: item.Publish && item.Publish.name ? item.Publish.name : "Untitled-1",
            id: item.id,
            isUpdating: false
        };
    }

    timeout = 0;
    componentWillMount() {
        this.onLoadCollectionList()
    }
    componentDidMount() {
        const { networks, feedData } = this.props
        this.setState({ networkData: networks, feedData: feedData.feeds })

    }
    componentWillReceiveProps(nextProps) {
        const { networks, feedData } = nextProps
        this.setState({ networkData: networks, feedData: feedData.feeds })
    }


    onClickToDelete = (data) => event => {
        Swal.fire({
            title: 'Are you sure?',
            text: "You want to delete this",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Yes, delete it!'
        }).then((result) => {
            if (result.isConfirmed) {
                var self = this;
                publishDeleteRequest(data).then((response) => {
                    self.onLoadPublish()
                    toast.success('Your widget has been deleted.')

                })


            }
        })

    }

    onTagApply = (value) => {
        const { tagApply } = this.state
        tagApply.push(value)
        this.setState({ tagApply })
    }
    onTagRemove = (index) => event => {
        let { tagApply } = this.state;
        tagApply.splice(index, 1)
        this.setState({ tagApply })
    }

    conditionApply = (condition) => event => {
        this.setState({ condition })
    }
    filterApply = (filterKey) => event => {
        const { chooseFilter } = this.state;
        if (chooseFilter[filterKey]) {
            delete chooseFilter[filterKey]
            this.setState({ chooseFilter: chooseFilter })
        }
        else {
            chooseFilter[filterKey] = true;
            this.setState({ chooseFilter })
        }

    }
    onSelectActive = (activeKey) => event => {
        this.setState({ activeKey: this.state.activeKey == activeKey ? "" : activeKey })
    }
    shareEmbedModal = event => this.setState({
        showGenratedCode: true
    })
    genratedCodeCallback = event => {
        this.setState({
            showGenratedCode: event
        })
    }
    renderSwitch = (platform) => {
        const { responseData } = this.state

        switch (platform) {
            case 'p-html':
                return <HtmlEmbed data={responseData} />;
            case 'p-wordpress':
                return <WordPressEmbed data={responseData} />;
            case 'p-wix':
                return <WixEmbed data={responseData} />;
            case 'p-squarespace':
                return <SquarespaceEmbed data={responseData} />;
            case 'p-shopify':
                return <ShopifyEmbed data={responseData} />;
            case 'p-other':
                return <OtherEmbed data={responseData} />;
            default:
                return <HtmlEmbed data={responseData} />;
        }

    }


    onSelectTemplate = (template) => event => this.setState({ selectedTemplate: template })

    onUgcRigths = (value) => event => {
        const { ugcRigths } = this.state;
        if (ugcRigths[value]) {
            delete ugcRigths[value]
            this.setState({ ugcRigths: ugcRigths })
        }
        else {
            ugcRigths[value] = true;
            this.setState({ ugcRigths })
        }
    }
    onSelectAllUgcRigths = event => {
        const { ugcRigths } = this.state;
        if (ugcRigths && Object.keys(ugcRigths).length >= 3) {
            this.setState({ ugcRigths: {} })
        }
        else {
            this.setState({
                ugcRigths: {
                    "1": true, "2": true, "3": true
                }
            })
        }
    }

    onconnectionApply = (value) => event => {
        const { selectedConnection } = this.state;
        if (selectedConnection[value]) {
            delete selectedConnection[value]
            this.setState({ selectedConnection: selectedConnection })
        }
        else {
            selectedConnection[value] = true;
            this.setState({ selectedConnection })
        }
    }
    onConnectionSelectAll = event => {
        const { selectedConnection, feedData } = this.state;
        if (selectedConnection && Object.keys(selectedConnection).length >= feedData.length) {
            this.setState({ selectedConnection: {} })
        }
        else {
            var dataNetwork = {}
            feedData.map((item) => {
                dataNetwork[item.feedId] = true
            })
            this.setState({
                selectedConnection: dataNetwork
            })
        }
    }
    onConnectionSearchFilter = event => {
        var searchText = event.target.value; // this is the search text
        if (this.timeout) clearTimeout(this.timeout);
        this.timeout = setTimeout(() => {
            this.setState({ textConnectionSearch: (searchText) ? String(searchText).toLowerCase() : searchText }, () => this.onLoadConnectionFilterData())
        }, 1000);
    }
    onLoadConnectionFilterData = () => {
        const { feedData } = this.props
        const { textConnectionSearch } = this.state;
        const searchData = feedData.filter((item) => String(item.name).toLowerCase().includes(textConnectionSearch))
        this.setState({ feedData: searchData })
    }
    onAssetsApply = (value) => event => {
        const { assetsApply } = this.state;
        if (assetsApply[value]) {
            delete assetsApply[value]
            this.setState({ assetsApply: assetsApply })
        }
        else {
            assetsApply[value] = true;
            this.setState({ assetsApply })
        }
    }

    onSelectAllAssetsApply = event => {
        const { assetsApply } = this.state;
        if (assetsApply && Object.keys(assetsApply).length >= 3) {
            this.setState({ assetsApply: {} })
        }
        else {
            this.setState({
                assetsApply: {
                    "1": true, "2,4": true, "3,5": true
                }
            })
        }
    }

    onNetworkApply = (value) => event => {
        const { selectedNetwork } = this.state;
        if (selectedNetwork[value]) {
            delete selectedNetwork[value]
            this.setState({ selectedNetwork: selectedNetwork })
        }
        else {
            selectedNetwork[value] = true;
            this.setState({ selectedNetwork })
        }
    }
    onClickSelectAll = event => {
        const { selectedNetwork, networkData } = this.state;
        if (selectedNetwork && Object.keys(selectedNetwork).length >= networkData.length) {
            this.setState({ selectedNetwork: {} })
        }
        else {
            var dataNetwork = {}
            networkData.map((item) => {
                dataNetwork[item.id] = true
            })
            this.setState({
                selectedNetwork: dataNetwork
            })
        }
    }
    onSearchFilter = event => {
        var searchText = event.target.value; // this is the search text
        if (this.timeout) clearTimeout(this.timeout);
        this.timeout = setTimeout(() => {
            this.setState({ textSearch: (searchText) ? String(searchText).toLowerCase() : searchText }, () => this.onLoadFilterData())
        }, 1000);
    }
    onLoadFilterData = () => {
        const { networks } = this.props
        const { textSearch } = this.state;
        const searchData = networks.filter((item) => String(item.name).toLowerCase().includes(textSearch))
        this.setState({ networkData: searchData })
    }

    onLoadCollectionList = () => {
        var self = this;
        const { collectionData } = this.props;
        if (collectionData && Object.keys(collectionData).length > 0 && collectionData.collectionList.length > 0) {
            self.setState({ collectionList: collectionData.collectionList, collectionCompleteList: collectionData.collectionList, assetsCount: collectionData.assetsCount })
        }
        else {
            getCollectionList().then((response) => {
                const { responseData } = response.data;
                if (responseData && Object.keys(responseData).length > 0) {
                    self.setState({ collectionList: responseData.collection, collectionCompleteList: responseData.collection })
                }
            }).catch((ex) => {

            })
        }

    }

    onCollectionApply = (value) => event => {
        const { selectedCollection } = this.state;
        if (selectedCollection[value]) {
            delete selectedCollection[value]
            this.setState({ selectedCollection: selectedCollection })
        }
        else {
            selectedCollection[value] = true;
            this.setState({ selectedCollection })
        }
    }
    onCollectionSelectAll = event => {
        const { selectedCollection, collectionList } = this.state;
        if (selectedCollection && Object.keys(selectedCollection).length >= collectionList.length) {
            this.setState({ selectedCollection: {} })
        }
        else {
            var dataCollection = {}
            collectionList.map((item) => {
                dataCollection[item.id] = true
            })
            this.setState({
                selectedCollection: dataCollection
            })
        }
    }
    onCollectionSearchFilter = event => {
        var searchText = event.target.value; // this is the search text
        if (this.timeout) clearTimeout(this.timeout);
        this.timeout = setTimeout(() => {
            this.setState({ collectionSearch: (searchText) ? String(searchText).toLowerCase() : searchText }, () => this.onLoadCollectionFilterData())
        }, 1000);
    }
    onLoadCollectionFilterData = () => {
        const { collectionSearch, collectionCompleteList } = this.state;
        const searchData = collectionCompleteList.filter((item) => String(item.name).toLowerCase().includes(collectionSearch))
        this.setState({ collectionList: searchData })
    }
    onChangeName = event => {
        this.setState({ publishCodeName: event.target.value })
    }
    onRequestPublish = event => {
        const { id, selectedTemplate, selectedCollection, selectedConnection, selectedNetwork, condition, showGenratedCode, ugcRigths, assetsApply, publishCodeName, tagApply } = this.state
        var self = this;

        var collection = []
        var template = ""
        var source = []
        var type = []
        var tags = []
        var network = []
        var ugcRigthsU = []

        if (ugcRigths && Object.keys(ugcRigths).length > 0) {
            Object.keys(ugcRigths).map((item) => {
                ugcRigthsU.push(item)
            })
        }
        if (selectedTemplate && Object.keys(selectedTemplate).length > 0 && selectedTemplate.id) {
            template = selectedTemplate.id
        }

        if (selectedNetwork && Object.keys(selectedNetwork).length > 0) {
            Object.keys(selectedNetwork).map((item) => {
                network.push(item)
            })
        }
        if (selectedCollection && Object.keys(selectedCollection).length > 0) {
            Object.keys(selectedCollection).map((item) => {
                collection.push(item)
            })
        }
        if (selectedConnection && Object.keys(selectedConnection).length > 0) {
            Object.keys(selectedConnection).map((item) => {
                source.push(item)
            })
        }
        if (assetsApply && Object.keys(assetsApply).length > 0) {
            Object.keys(assetsApply).map((item) => {
                type.push(item)
            })
        }
        if (tagApply && tagApply.length > 0) {
            tags = tagApply

        }
        if ((selectedTemplate && Object.keys(selectedTemplate).length > 0 && selectedTemplate.id) && (collection.length > 0 || source.length > 0 || tags.length > 0 || type.length > 0 || ugcRigthsU.length > 0 || network.length > 0)) {
            const data = {
                id: id,
                name: publishCodeName,
                "template": template,
                "collection": collection.join(","),
                "source": source.join(","),
                "assets_type": type.join(","),
                "tags": tags.join(","),
                "ugc_status": ugcRigthsU.join(","),
                "publish_type": 1,
                "campaign_type": "",
                "screen_type": "",
                "platform": "",
                network: network.join(","),
                filterType: String(condition).toUpperCase()

            }
            this.setState({ isUpdating: true })
            updatePublishRequest(data).then((response) => {
                const { status, response_code, response_message } = response.data
                if (status == "success") {
                    toast.success("Publish widget successfully updated.")
                    self.setState({ isUpdating: false }, () => self.props.onLoadPublish())
                    //self.setState({ responseData: response.data }, () => self.props.onLoadPublish())
                }
                else toast.error(response_message)


            }).catch((ex) => {

            })
        }
    }
    onResetFilter = event => {
        this.setState({
            showGenratedCode: 'p-html',
            chooseFilter: {},
            collectionApply: [],
            sourceApply: {},
            assetsApply: {},
            tagApply: [],
            ugcRigths: {},
            selectedTemplate: null,
            condition: "and",
            selectedNetwork: {},
            selectedConnection: {},
            textSearch: "",
            textConnectionSearch: "",
            selectedCollection: {},
            collectionSearch: "",
            genrateCode: false,
            responseData: null,
            activeFilter: null,
            publishCodeName: "",
            isUpdating: false

        })
    }

    render() {
        const { showGenratedCode, chooseFilter, genrateCode, responseData, activeFilter } = this.state
        const { networks, history } = this.props
        return <>
            <WebEmbedAside genratedCodeCallback={this.genratedCodeCallback} genratedCodeModal={showGenratedCode} filterApply={this.filterApply} state={this.state} onSelectTemplate={this.onSelectTemplate} onSelectAllUgcRigths={this.onSelectAllUgcRigths} onUgcRigths={this.onUgcRigths} onTagRemove={this.onTagRemove} onTagApply={this.onTagApply} onAssetsApply={this.onAssetsApply} onconnectionApply={this.onconnectionApply} onSelectAllAssetsApply={this.onSelectAllAssetsApply} onNetworkApply={this.onNetworkApply} onClickSelectAll={this.onClickSelectAll} onSearchFilter={this.onSearchFilter} onCollectionSearchFilter={this.onCollectionSearchFilter}
                onCollectionSelectAll={this.onCollectionSelectAll}
                onCollectionApply={this.onCollectionApply} conditionApply={this.conditionApply} onConnectionSearchFilter={this.onConnectionSearchFilter} onConnectionSelectAll={this.onConnectionSelectAll} onClickToDelete={this.onClickToDelete} onResetFilter={this.onResetFilter} activeFilter={activeFilter} history={history} onChangeName={this.onChangeName} onUpdateReponseData={this.onUpdateReponseData} onLoadPublish={this.onLoadPublish} onSelectActive={this.onSelectActive} onRequestPublish={this.onRequestPublish} isPublishEdit={true} />
        </>


    }
}
const mapStateToProps = (state) => {
    return {
        networks: state.networks.networkData,
        feedData: state.feedData.filterList,
        collectionData: state.collection,
        templeteList: state.templates.templeteList

    }
}

export default connect(mapStateToProps)(WebEmbed);